import React, { Component } from "react";
import "./Sidebar.css";
import { categorys } from '../category';


const left = '<';
const right = '>';

const menu1 = [
    "웹 개발", [
        "React",
        "Node",
    ],
    "보안" , [
        "Pwnable",
        "Web",
    ],
    "블로그" , []
]
class Sidebar extends Component {

    constructor() {
        super();
        this.state = {
            sidebar: true
        }


    }
    toggleSidebar = () => {
        this.setState({sidebar : !this.state.sidebar});
    }
    componentWillMount() {
        console.log(categorys);

    }


    render() {
        const category = categorys.map((category) => (
            <a href={'/home/#/category/'+category.category}>
            <div className={"CategoryTitle"}>{category.title}
            </div>
            </a>
        ));
        return (
            <div>
                <div className={ this.state.sidebar ? "Sidebar" : "SidebarHidden"}>
                    <button
                        className={"ToggleButton"}
                        onClick={this.toggleSidebar}>
                        {right}
                    </button>

                    {category}
                </div>
                <button

                    className={ this.state.sidebar ? "SidebarHidden" : "SidebarShow"}
                    onClick={this.toggleSidebar}>
                    {left}
                </button>
            </div>
        );
    }
}

export { Sidebar }
