import React, { Component } from "react";
import ReactMarkdown from 'react-markdown/with-html';
import './Home.css';
import {MarkdownViewer} from "./MarkdownViewer";
const profile = require('../articles/프로필/whoami.md');

class Home extends Component{

    constructor(props) {
        super(props);
        this.state = {
            profile: ''
        }
        fetch(profile).then(res => res.text()).then(text => this.setState({ profile: text }));

    }

    render() {
        return (
            <div className={"Home"}>
                <ReactMarkdown source={this.state.profile}/>
            </div>
        );
    }
}


export { Home };
