import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown/with-html';
import { categorys } from '../category';

class MarkdownViewer extends Component{
    constructor(props) {
        super(props);
        this.state = {
            categoryInfo : {
                title: '',
                category: '',
                articles: [],
            },
            markdown: '',
            contents: '',
        }
    }
    componentDidMount() {
        for(var category of categorys){
            console.log(this.props.match.params.category);
            console.log(category);

            if(category.category === this.props.match.params.category){
                //this.setState({categoryInfo: category});
                console.log(this.props.match.params.title);
                for(var article of category.articles){
                    if(article.title === this.props.match.params.title){
                        //this.setState({markdown: article.article});
                        console.log(this.state.markdown);
                        fetch(article.article).then(res => res.text()).then(text => this.setState({ markdown: text }));
                        break;
                    }
                }
                break;
            }
        }
    }

    render() {
        return (
            <div style={{textAlign: 'left', padding: '30px'}}>
                <ReactMarkdown
                    source={this.state.markdown}
                    escapeHtml={false}/>
            </div>
        );
    }

}
export { MarkdownViewer };
