import React, { Component } from 'react';
import { categorys } from '../category';
import './ArticleList.css';

var categoryInfo;
class ArticleList extends Component {


    constructor(props) {
        super(props);

        this.state = {
            categoryInfo : {
                title: '',
                category: '',
                articles: [],
            },
            articleComponents : '',
        }

    }
    componentDidMount() {
        for (var category of categorys) {
            console.log(category);
            if (category.category === this.props.match.params.category) {
                this.setState({categoryInfo: category});
                console.log(category)
                break;
            }
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot)
    {
        console.log('!')
        if(prevProps.match.params.category !== this.props.match.params.category
            || prevState.categoryInfo !== this.state.categoryInfo) {
            for (var category of categorys) {
                console.log(category);
                if (category.category === this.props.match.params.category) {
                    this.setState({categoryInfo: category});
                    console.log(category)
                    break;
                }
            }
        }
    }

    render() {
        const articles = this.state.categoryInfo.articles.map((article) => (
            <a href={'/home/#'+this.props.location.pathname+'/'+article.title}>
                <li className={"ArticleTitle"}>
                {article.title}
                </li>
            </a>
        ));
        return (
            <div className={"Container"}>
                <h2 className={"CategoryName"}>{this.state.categoryInfo.title}</h2>
                <div>{articles}</div>
            </div>
        );
    }
}

export { ArticleList };
